.backdrop {
  opacity: .5;
  pointer-events: none;
  background: #ceffff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loading {
  display: flex;
}

.loading .dot {
  border-radius: 50%;
  width: 2em;
  height: 2em;
  margin: .8em;
  position: relative;
}

.loading .dot:before {
  content: "";
  background: inherit;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  animation: 2s ease-out infinite wave;
  position: absolute;
}

.loading .dot:first-child {
  background: #7ef9ff;
}

.loading .dot:first-child:before {
  animation-delay: .2s;
}

.loading .dot:nth-child(2) {
  background: #89cff0;
}

.loading .dot:nth-child(2):before {
  animation-delay: .4s;
}

.loading .dot:nth-child(3) {
  background: #4682b4;
}

.loading .dot:nth-child(3):before {
  animation-delay: .6s;
}

.loading .dot:nth-child(4) {
  background: #0f52ba;
}

.loading .dot:nth-child(4):before {
  animation-delay: .8s;
}

.loading .dot:nth-child(5) {
  background: navy;
}

.loading .dot:nth-child(5):before {
  animation-delay: 1s;
}

@keyframes wave {
  50%, 75% {
    transform: scale(2.5);
  }

  80%, 100% {
    opacity: 0;
  }
}

